.navBar {
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  backdrop-filter: blur(10px);
  position: fixed;
  top: 0;
  z-index: 99999;

  .logo {
    display: flex;
    align-items: center;
    img {
      height: 50px;
      width: 50px;
    }
    h1 {
      font-family: "Press Start 2P", cursive;
      text-shadow: 0 0 5px black;

      padding-left: 10px;

      span {
        color: white;
        text-shadow: 0 0 5px violet;
      }
    }
  }
  .navUl {
    width: 500px;
    display: flex;
    justify-content: space-around;
    p {
      letter-spacing: 2px;
      font-size: 15px;
      font-family: "Gemunu Libre", sans-serif;
    }
  }
}
@media (min-width: 500px) {
  .navBar {
    padding: 0 50px;
    width: 100%;
    .navBurger {
      display: none;
    }
    h1 {
      color: rgb(146, 102, 243);
      font-size: 22px;
    }
  }
}

@media (max-width: 500px) {
  .navBar {
    width: 100vw;
    // justify-content: center;
    padding: 15px;
    .navBurger {
      display: block;
      letter-spacing: 2px;
      transform: rotate(90deg);
    }
    .navUl {
      display: none;
    }
    h1 {
      font-size: 15px;
      color: white;
      span {
        display: none;
      }
    }
  }
}
